p{
    font-size:10vw;
    color:white;
    line-height:1.2;
}

input[type=text] {
    width:50vw;
    font-family:'dunkel';
    font-size:10vw;
    background-color:rgba(0,0,0,0);
    box-sizing: border-box;
    border: none;
    color:white;
    font-variation-settings: "wdth" 1000;
}

input:focus {
    outline: none;
  }

form span{
    color:white;
    font-variation-settings: "wdth" 1000;
    font-family:'dunkel';
    font-size:10vw;
}

button{
    height:23vh;
    width:19vw;
    padding:0;
    border-radius: 50%;
}

button:focus {
    outline: none;
  }

button span{
    font-family:'dunkel';
    font-size:5vw;
    vertical-align: 20%;
    font-variation-settings: "wdth" 1000;
}

#money-input{
    display:inline;
}

.manual-form-con #money-input{
    width:20vw;
}

.line{
    background-color:white;
    width:90%;
    height:1px;
    margin-top:20px;
    margin-bottom:20px;
}

  #money-form-con{
      padding-left:1.2%;
      max-width:70%;
      position: relative;
  }
  #money-form{
      margin-bottom:25px;
  }